import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Info from './panels/Info';
import Pay from './panels/Pay';
import Task from './panels/Task';
import Task_1 from './panels/Task_1';
import Task_2 from './panels/Task_2';
import Task_3 from './panels/Task_3';
import Task_4 from './panels/Task_4';
import Task_5 from './panels/Task_5';
import Task_Pubg from './panels/Task_Pubg';
import Greendemand from './panels/Greendemand';

import Test from './panels/test';

import Rules from './panels/Rules';
import Profile from './panels/Profile';
import SendMoney from './panels/SendMoney';
import Shop from './panels/Shop';
import InfoApp from './panels/InfoApp';



const App = () => {
	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);

	useEffect(() => {
		
		async function fetchData() {
			const user = await bridge.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	return (
		<View activePanel={activePanel} popout={popout}>
			<Home id='home' fetchedUser={fetchedUser} go={go} />
			<Persik id='persik' fetchedUser={fetchedUser} go={go}/>
			<Profile id='profile' fetchedUser={fetchedUser} go={go} />
			<Info id='info' go={go} />
			<Pay id='pay' go={go} />
			<SendMoney id='sendmoney' go={go} />
			<Shop id='shop' go={go} />
			<InfoApp id='infoapp' go={go} />
			<Task id='task' go={go} />
			<Task_1 id='task_1' go={go} />
			<Task_2 id='task_2' go={go} />
			<Task_3 id='task_3' go={go} />
			<Task_4 id='task_4' go={go} />
			<Task_5 id='task_5' go={go} popout={setPopout}  />
			<Rules id='rules' go={go}  />
			<Task_Pubg id='task_pubg' go={go} />
			<Greendemand id='greendemand' go={go} />
			<Test id='test' go={go} popout={setPopout} />
			
	
		</View>
	);
}

export default App;

