import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import {
    PanelHeaderBack,
    List,
    MiniInfoCell,
    RichCell,
    Placeholder,
    Radio,
    Cell,
    Separator,
    Link,
    platform,
    CellButton,
    Header,
    InfoRow,
    Counter,
    PanelHeaderButton,
    getElementById,
    Alert,
    Banner,
    IOS,
    Title,
    Text
} from '@vkontakte/vkui';

import green from '../img/green.png';

const Test = ({id, go, popout}) => (
    <Panel id={id} centered={false}>
        <PanelHeader
            left={<PanelHeaderBack onClick={go} data-to="home"/>}
        >
            Alert
        </PanelHeader>


        <Div>
        <Banner
        before={<img className="Tochka" width={30} src={green}/>}
        subheader={
          <React.Fragment>
           Спрос на задание повышен<br />
            Сумма выплаты больше средней.
          </React.Fragment>
        }
        actions={<Button mode="tertiary" onClick={() => {
                    popout(
                        <Alert
                            actions={[{
                                title: 'test',
                                autoclose: true,
                                mode: 'cancel'
                            }]}
                            onClose={() => {
                                popout(null)
                            }}
                        >
                            <h2>Подтвердите действие</h2>
                            <p>Добавить пользователю право на модерацию контента.</p>
                        </Alert>
                    )
                }}>Подробнее</Button>}
      />
    
        </Div>


    </Panel>

);


Test.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
  popout: PropTypes.func.isRequired
};

export default Test;