import React from 'react';
import PropTypes from 'prop-types';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import {
    PanelHeaderBack,
    List,
    MiniInfoCell,
    RichCell,
    Placeholder,
    Radio,
    Cell,
    Separator,
    Link,
    platform,
    CellButton,
    Header,
    Avatar,
    Subhead,
    InfoRow,
    Counter,
    PanelHeaderButton,
    getElementById,
    Alert,
    Banner,
    IOS,
    Title,
    Text
} from '@vkontakte/vkui';

import Icon28DocumentOutline from '@vkontakte/icons/dist/28/document_outline';
import Icon28MoneyHistoryBackwardOutline from '@vkontakte/icons/dist/28/money_circle_outline';

import green from '../img/green.png';
import survivalmaster3d from '../img/survivalmaster3d.png';
import persik from '../img/persik.png';
import logo from '../img/logo.svg';
import android from '../img/android.svg';
import apple from '../img/apple.svg';
import './Persik.css';
import './Tochka.css';

const Task_5 = ({id, go, popout}) => (
    <Panel id={id} centered={false}>
        <PanelHeader
            left={<PanelHeaderBack onClick={go} data-to="persik"/>}
        >
            Survival Master 3D
        </PanelHeader>


        <Div>
        <Banner
        before={<img className="Tochka" width={30} src={green}/>}
        subheader={
          <React.Fragment>
           Спрос на задание повышен<br />
            Сумма выплаты стала больше средней.
          </React.Fragment>
        }
        actions={<Button mode="tertiary" onClick={() => {
                    popout(
                        <Alert
                            actions={[{
                                title: 'Понятно',
                                autoclose: true,
                                mode: 'cancel'
                            }]}
                            onClose={() => {
                                popout(null)
                            }}
                        >
                            <h2>Повышенный спрос</h2>
                            <p>Спрос на данное задание повышен. Это происходит когда задание пользуется популярностью, в таком случае сумма выплаты за отчет будет больше.</p>
                        </Alert>
                    )
                }}>Подробнее</Button>}
      />
    
       
        <Group header={<Header mode="secondary">АКТИВНО ДО 24.05.2021</Header>}></Group>
 <Div><Cell before={<Avatar mode="app" src={survivalmaster3d} />} description="Lion Studios">Survival Master 3D</Cell></Div>
<Div style={{ marginTop:-35}}>
                        <Cell
          description="Survival Master 3D"
        >
          Название
        </Cell>
        </Div>
        <Div style={{marginTop:-40}}>

                                <Cell
          description="1.4"
        >
          Версия
        </Cell>
        </Div>
        <Div style={{ marginTop:-40}}>
        <Cell><img className="apple" width={17} src={apple}/><img className="android" width={17} src={android}/></Cell>
        </Div>

  
 <Separator wide />

<Header mode="secondary">Задачи: <Div style={{ paddingTop: -35, paddingBottom: 10 }}>
           <h5>1. Выявить нестабильную работу при <br/> прицеливании; <br/> 2. Выявить нестабильную работу при <br/> передвижении; <br/> 3. Выявить дополнительные ошибки из <br/> доступных категории.</h5>
          </Div></Header>

        <Cell>

<Subhead weight="semibold" style={{ marginBottom: 16 }}>ВЫПЛАТА ЗА ОТЧЕТ:<div className="price_task_1">      <Header indicator={<Icon28MoneyHistoryBackwardOutline />}>
        71 РУБ.
      </Header></div></Subhead>
  
        </Cell>

  <center>
  

                     <Div>
        <Button before={<Icon28DocumentOutline/>} size="l" href="https://vk.com/app5619682_-186420586#545373">Отправить отчет</Button>
     </Div>
      </center>
</Div>


    </Panel>

);


Task_5.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
    fetchedUser: PropTypes.shape({
        photo_200: PropTypes.string,
        first_name: PropTypes.string,
        last_name: PropTypes.string,
        city: PropTypes.shape({
            title: PropTypes.string,
        }),
    }),
  popout: PropTypes.func.isRequired
};

export default Task_5;